.ImageGallery {
    display: grid;
    max-width: calc(95vw - 215px);
    grid-template-columns: repeat(auto-fill, minmax(165px, 1fr));
    grid-gap: 16px;
    margin-top: 10px;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.ImageGalleryItem_image {
    width: 100%;
    height: 260px;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem_image:hover {
    transform: scale(1.03);
    cursor: pointer;
}